import { BigNumber } from "ethers";
import { useCallback } from "react";
import { useTransactionAdder } from "../../../state/transactions/hooks";

import useCore from "../../useCore";
import { useAddPopup } from "../../../state/application/hooks";
import { formatToBN, getDisplayBalance } from "../../../utils/formatBalance";
import formatErrorMessage from "../../../utils/formatErrorMessage";
import { useGetActiveChainId } from "../../../state/chains/hooks";
import { getCollateralPrices } from "../../../utils/getCollateralPrices";

const useLockWithETH = (
  ethVal: number) => {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetActiveChainId();

  return useCallback(
    async (onSuccess: () => void, onFailure: () => void): Promise<void> => {
      try {
        let prices = await getCollateralPrices()
        let mahaUsd = prices.MAHA 
        let ethUsd = prices.ETH
        const oneEthMahaVal = ethUsd / mahaUsd
        const mahaVal = ethVal * oneEthMahaVal
        // console.log("useLockWithETH", ethVal, mahaVal)
        const contract = core.getEthMahaxLockerContract(chainId);
        const response = await contract.swapETHforLocks(ethVal, 1, formatToBN(mahaVal), false);

        // 1% slippage needs to be calculated

        addTransaction(response, {
          summary: `Lock ${Number(
            getDisplayBalance(formatToBN(ethVal), 18, 3)
          ).toLocaleString("en-US", { minimumFractionDigits: 3 })} ETH.`,
        });

        const tx = await response.wait();

        if (tx?.status === 1) onSuccess();
        if (tx?.status !== 1) onFailure();
      } catch (e: any) {
        console.log(e);
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
        onFailure();
      }
    },
    [core, ethVal, addPopup, addTransaction]
  );
};

export default useLockWithETH;
