import {useEffect, useMemo, useState} from 'react'
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import { makeStyles, Theme, createStyles, withStyles } from '@material-ui/core/styles';
import Slider from '@material-ui/core/Slider';
import { ConnectButton } from '@rainbow-me/rainbowkit';
import { useAccount, useNetwork } from 'wagmi';
import { Grid } from '@material-ui/core';

import "./MintModal.css"
import CloseIcon from '../images/icons/Close.png'
import modal_divider from '../images/vectors/modal_divider.svg'
import slider_thumb from '../images/icons/slider_thumb.svg'
import Male_Icon from '../images/icons/Male_Icon.svg'
import Female_Icon from '../images/icons/Female_Icon.svg'
import Up_Arrow from '../images/icons/Up_Arrow.svg'
import Eth_Logo_Black from '../images/icons/Eth_Logo_Black.svg'
import Maha_logo_White from '../images/icons/Maha_logo_White.svg'
import useTokenBalance from '../hooks/useTokenBalance';
import useCore from '../hooks/useCore';
import { useGetActiveChainId } from '../state/chains/hooks';
import { formatToBN, getDisplayBalance } from '../utils/formatBalance';
import useLockMAHA from '../hooks/callbacks/Minting/useLockMAHA';
import { DEFAULT_SINGLE_DATEINPUT_STATE, fourYearFromNow, MAXTIME, MAXTIME_IN_MS, YEAR_IN_MS } from '../utils/constants';
import { useGetSliderMarks } from '../utils';
import useApprove, { ApprovalState } from '../hooks/callbacks/useApprove';
import useGetNativeTokenBalance from '../hooks/state/useGetNativeTokenBalance';
import useLockWithETH from '../hooks/callbacks/Minting/useLockWithETH';
import { useGetMahaToEth } from '../hooks/state/useGetMahaToEth';
import { BigNumber } from 'ethers';

type genderAndSkinType = {
  gender: 'Male' | 'Female' | string, 
  skin: 'Fair' | 'Brown' | 'Dark' | string,
  // mintWith: 'MAHA' | 'ETH' | string
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    modal: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      // width: 492,
    },
    paper: {
      backgroundColor: '#FFEDE1',
      boxShadow: theme.shadows[5],
      padding: 32,
      // width: 492,
      borderRadius: 8
    },
  }),
);

let trackBg = '#EE8CB1'

const PrettoSlider = withStyles({
  root: {
    color: '#52af77',
    height: 8,
    
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundImage: `url(${slider_thumb})`,
    //   border: '2px solid currentColor',
    marginTop: -8,
    marginLeft: -12,
    '&:focus, &:hover, &$active': {
      boxShadow: 'inherit',
    },
    backgroundColor: 'transparent',
   
  },
  active: {},
  valueLabel: {
    left: 'calc(-50% + 8px)',
  },
  track: {
    height: 10,
    borderRadius: 40,
    backgroundColor: trackBg,
  },
  rail: {
    height: 8,
    borderRadius: 4,
  },
},)(Slider);

function valuetext(value: number) {
  return `${value} %`;
}

const MintModal = (props: any) => {
  const classes = useStyles();
  const core = useCore()
  const { isConnected, address, status } = useAccount()
  const {chain} = useNetwork()
  const activeChain = useGetActiveChainId()
  let chainId = activeChain
  
  const [genderAndSkin, setGenderAndSkin] = useState<genderAndSkinType>({
    gender: 'Male',
    skin: 'Brown',
  })
  const [mintWithToken, setMintWithToken] = useState<string>('MAHA')
  const [sliderVal, setSliderVal] = useState<number>( mintWithToken === 'MAHA' ? 100 : 0.04482939)

  const tokenMahaBal = useTokenBalance(core._tokens[chainId]['MAHA'])
  const tokenEthBal = useGetNativeTokenBalance()
  const commonEthMax = useGetMahaToEth(300)
  const rareEthMax = useGetMahaToEth(5000)
  const legendaryEthMax = useGetMahaToEth(25000)

  let finalTokenBal =
    mintWithToken === 'MAHA' ?
      !tokenMahaBal.isLoading ? Number(getDisplayBalance(tokenMahaBal.value, 18, 6)) : 0 :
      !tokenEthBal.isLoading ? Number(getDisplayBalance(tokenEthBal.value, 18, 6)) : 0

  const handleClose = () => {
    props.handleClose()
  };

  const handleChange = (event: any, newValue: number | number[]) => {
    setSliderVal(newValue as number);
  };

  const genderAndSkinHandler = (value: string, type: string) => {
    if(type === 'gender')
      setGenderAndSkin(prevState => ({
        ...prevState,
        gender: value
      }))
    else if(type === 'skin')
      setGenderAndSkin(prevState => ({
        ...prevState,
        skin: value
      }))
  }

  const [approveStatus, approve] = useApprove(
    core._tokens[chainId]['MAHA'],
    core.getVestingContract(chainId)?.address || '',
  );

  const isMahaApproved = useMemo(
    () => approveStatus === ApprovalState.APPROVED,
    [approveStatus]
  );
  const isMahaApproving = useMemo(
    () => approveStatus === ApprovalState.PENDING,
    [approveStatus]
  );

  const lockMAHAAction = useLockMAHA(
    formatToBN(sliderVal),
    Math.round((Number(MAXTIME_IN_MS + Date.now()) - Number(new Date().setHours(0, 0, 0, 0))) / 1000), 
    false,
  )

  const lockETHAction = useLockWithETH(sliderVal)

  const handleMint = () => {
    if(mintWithToken === 'MAHA'){
      lockMAHAAction(() => {
        window.location.reload()},
        () => {
        }
      ).then(() => {
      })
    }
    else {
      lockETHAction(() => {
        window.location.reload()
      }, () => {})
        .then(() => {})
    }
  }

  const handleChangeCheck = (event: any) => {
    let isChecked = event.target.checked
    if(isChecked) setMintWithToken("ETH")
    else setMintWithToken("MAHA")
  }

  const marks = useGetSliderMarks(mintWithToken)

  const mahaXReceived: string = useMemo(() => {
    if(mintWithToken !== 'MAHA')
      return BigNumber.from(0).toString();
    const bn: BigNumber = formatToBN(sliderVal, 18);
    if (bn.lte(0)) return BigNumber.from(0).toString();
  
    const timeDiff = (Date.now() + MAXTIME_IN_MS) - Date.now();
    if (timeDiff <= 0 || !timeDiff) return BigNumber.from(0).toString();

    const value = bn.mul(timeDiff).div(YEAR_IN_MS).div(4);
    return Math.ceil(Number(getDisplayBalance(value, 18, 3)))
      .toLocaleString('en-US', { minimumFractionDigits: 3 });
  }, [sliderVal]);

  return(
    <div id={'Mint-Modal'}>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={props.open}
        onClose={handleClose}
        className={classes.modal}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}>
        <Fade in={props.open}>
          <div className={classes.paper}>
            <div className='mint-modal-heading'>
              <div>MINT NFT</div>
              <div onClick={handleClose}><img src={CloseIcon} alt={'CloseIcon'} /></div>
            </div>
            <img src={modal_divider} alt={'modal_divider'} />
            <div style={{padding: 0, margin: 0}}>
            <input type="checkbox" onChange={handleChangeCheck} id="switch" />
            <div className="app">
              <div className="body">
                <div className="content">
                  <label htmlFor="switch">
                    <div className="toggle"></div>
                    <div className="names">
                      <div className="light">
                        <div><img src={Maha_logo_White} alt={'Maha_logo_White'} /></div>
                        <div>MAHA</div>
                      </div>
                      <div className="dark">
                        <div><img src={Eth_Logo_Black} alt={'Eth_Logo_Black'} /></div>
                        <div>ETH</div>
                      </div>
                    </div>
                  </label>
                </div>
              </div>
            </div>
            </div>
            <div>
              <div className='modal-content-heading'>Supply</div>
              <div className='flex-space-between'>
                <div className='modal-content-values'> {sliderVal} {mintWithToken === 'MAHA' ? 'MAHA' : 'ETH'} &nbsp;
                  <span className='modal-content-values' style={{color: 'rgba(0, 0, 0, 0.3)'}}></span>
                </div>
                <div className='modal-content-values'>Balance: {finalTokenBal} {mintWithToken === 'MAHA' ? 'MAHA' : 'ETH'} &nbsp;
                  <span className='modal-content-values' style={{color: 'rgba(0, 0, 0, 0.3)'}}></span>
                </div>
              </div>
              <div>
                {
                  mintWithToken === 'MAHA' ?
                    <div>
                      <PrettoSlider
                        step={1}
                        valueLabelDisplay="auto"
                        aria-label="pretto slider"
                        defaultValue={100}
                        marks={marks}
                        getAriaValueText={valuetext}
                        value={sliderVal}
                        onChange={handleChange}
                        min={100}
                        max={Number(finalTokenBal)}
                        disabled={finalTokenBal < 100}
                      /> 
                      {
                        finalTokenBal < 100 ? 
                        <div className='marginV modal-content-heading' style={{color: 'red'}}>Your minimum balance should be 100 Maha</div>
                        : ''
                      }
                       <div style={{position: 'relative', marginBottom: '60px'}}>
                        <div className='modal-content-heading mb16'>Chances</div>
                        <div style={{display: 'flex', marginBottom: '10px'}}>
                          <div className='rarity-text' style={{flex: 5, backgroundColor: '#1D97DE'}}>Common</div>
                          <div className='rarity-text' style={{flex: 4, backgroundColor: '#EE8CB1'}}>Rare</div>
                          <div className='rarity-text' style={{flex: 2, backgroundColor: '#2E4D6E'}}>Legendary</div>
                          <div className='rarity-text' style={{flex: 1, backgroundColor: '#DF3E00'}}>1/1s</div>
                        </div>
                        <div style={{position: 'absolute', 
                          left: sliderVal > 25000 ? `430px` :
                            sliderVal > 5000 ? `calc(100*${sliderVal}px/25000 + 300px)`: 
                              sliderVal > 300 ? `calc(160*${sliderVal}px/5000 + 148px)` : 
                                `calc(0.492px * ${sliderVal})`}}>
                          <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center', position: 'relative', top: -10}}>
                            <img src={Up_Arrow} alt={'Up_Arrow'} height={'11px'} width={'11px'} />
                            <div className='modal-content-heading'>Your Chances</div>
                          </div>
                        </div>
                      </div>
                    </div>
                  :
                    <div>
                      <PrettoSlider
                        step={0.0001}
                        valueLabelDisplay="auto"
                        aria-label="pretto slider"
                        defaultValue={0.04482939}
                        marks={marks}
                        getAriaValueText={valuetext}
                        value={sliderVal}
                        onChange={handleChange}
                        min={0.04482939}
                        max={Number(finalTokenBal)}
                        disabled={finalTokenBal < 0.04482939}
                      />
                      {
                        finalTokenBal < 0.04482939 ? 
                        <div className='marginV modal-content-heading' style={{color: 'red'}}>Your minimum balance should be {0.04482939} ETH</div>
                        : ''
                      }
                      <div style={{position: 'relative', marginBottom: '60px'}}>
                        <div className='modal-content-heading mb16'>Chances</div>
                        <div style={{display: 'flex', marginBottom: '10px'}}>
                          <div className='rarity-text' style={{flex: 5, backgroundColor: '#1D97DE'}}>Common</div>
                          <div className='rarity-text' style={{flex: 4, backgroundColor: '#EE8CB1'}}>Rare</div>
                          <div className='rarity-text' style={{flex: 2, backgroundColor: '#2E4D6E'}}>Legendary</div>
                          <div className='rarity-text' style={{flex: 1, backgroundColor: '#DF3E00'}}>1/1s</div>
                        </div>
                        <div style={{position: 'absolute', 
                          left: sliderVal > legendaryEthMax ? `430px` :
                            sliderVal > rareEthMax ? `calc(9*${sliderVal}px + 300px)`: 
                              sliderVal > commonEthMax ? `calc(71*${sliderVal}px + 148px)` : 
                                `calc(0.492px * ${sliderVal} * 2250)`}}>
                          <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center', position: 'relative', top: -10}}>
                            <img src={Up_Arrow} alt={'Up_Arrow'} height={'11px'} width={'11px'} />
                            <div className='modal-content-heading'>Your Chances</div>
                          </div>
                        </div>
                      </div>
                    </div>
                }
              </div>
            </div>
           
            {/* <Grid container spacing={2} className={'mb24'}>
              <Grid item xs={5}>
                <div className='modal-content-heading mb8'>Gender</div>
                <div className='flex-start-center'>
                  <div 
                    onClick={() => genderAndSkinHandler('Male', 'gender')} 
                    className={`gender-option mr8 ${genderAndSkin.gender === 'Male' ? 'gender-option-selected' : ''}`}>
                    <img src={Male_Icon} alt={'Male_Icon'} className={'mr8'} />
                    <div 
                      className='modal-content-heading' 
                      style={{color:'#0F0F13'}}>
                        Male
                    </div>
                  </div>
                  <div 
                    onClick={() => genderAndSkinHandler('Female', 'gender')} 
                    className={`gender-option ${genderAndSkin.gender === 'Female' ? 'gender-option-selected' : ''}`}>
                    <img src={Female_Icon} alt={'Female_Icon'} className={'mr8'} />
                    <div 
                      className='modal-content-heading' 
                      style={{color:'#0F0F13'}}>
                        Female
                    </div>
                  </div>
                </div>
              </Grid>
              <Grid item xs={7}>
                <div className='modal-content-heading mb8'>Skin Color</div>
                <div className='flex-start-center skin-option-container'>
                  <div 
                    onClick={() => genderAndSkinHandler('Fair', 'skin')} 
                    className={`skin-option mr8 ${genderAndSkin.skin === 'Fair' ? 'skin-option-selected' : ''}`}>
                    <div 
                      className='modal-content-heading text-center' 
                      style={{color:'#fff'}}>Fair
                    </div>
                  </div>
                  <div 
                    onClick={() => genderAndSkinHandler('Brown', 'skin')} 
                    className={`skin-option mr8 ${genderAndSkin.skin === 'Brown' ? 'skin-option-selected' : ''}`}>
                    <div 
                      className='modal-content-heading text-center' 
                      style={{color:'#fff'}}>Brown
                    </div>
                  </div>
                  <div 
                    onClick={() => genderAndSkinHandler('Dark', 'skin')} 
                    className={`skin-option ${genderAndSkin.skin === 'Dark' ? 'skin-option-selected' : ''}`}>
                    <div 
                      className='modal-content-heading text-center' 
                      style={{color:'#fff'}}>Dark
                    </div>
                  </div>
                </div>
              </Grid>
            </Grid> */}
            <div className='modal-hr mb16'></div>
            <div className='flex-space-between mb12'>
              <div className='modal-bottom-val-title'>Locked for</div>
              <div className='modal-bottom-val'>4 Years</div>
            </div>
            <div className='flex-space-between mb16'>
              <div className='modal-bottom-val-title'>MahaX Received</div>
              <div className='modal-bottom-val'>{mahaXReceived} MAHA</div>
            </div>
              {
                !isConnected ? 
                  <ConnectButton /> : 
                  chain?.id !== 1 ?
                    <div>Connect to Ethereum Mainnet</div> :
                      mintWithToken === 'MAHA' ?
                  isMahaApproved ? 
                  <button type="button" className='primary-button primary-modal-button-text' onClick={handleMint}>
                    Mint NFT
                  </button>
                  :
                  <button type="button" className='primary-button primary-modal-button-text' onClick={approve}>
                    Approve
                  </button>
                  :
                  <button className='primary-button primary-modal-button-text' onClick={handleMint}>Mint NFT</button>
              }
          </div>
        </Fade>
      </Modal>
    </div>
  )
}

export default MintModal
