import React from 'react'
import {Mixpanel} from "../../analytics/Mixpanel";

interface IProps {
  trackingid: string;
}

type Props =  React.DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement> & IProps

const Button = (props: Props) => {
  const handleClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    if (props.onClick) props.onClick(e);
    if (props.trackingid ) {
      Mixpanel.track(props.trackingid, {type: 'button'});
    }
  }

  return (
    <button
      {...props}
      onClick={handleClick}
    >
      {props.children}
    </button>
  )
}


export default Button;
