import { Provider } from "react-redux";
import React, { useEffect } from 'react';
import { SnackbarProvider } from "notistack";
// import { useWallet, UseWalletProvider } from 'use-wallet';
import { HashRouter as Router } from 'react-router-dom';

import './index.css';

// import Popups from "./components/Popups";

import Navigation from './Navigation';
import ModalsProvider from './context/Modals';
import {ProtocolProvider} from './context/ProtocolProvider';

import store from "./state";
import { ConfigChains, getChainsRpc, getSupportedChains } from './config';
import useCore from "./hooks/useCore";
import Updaters from "./state/Updaters";

import { useGetUpdateActiveChainId } from "./state/chains/hooks";
import "@rainbow-me/rainbowkit/styles.css";
import {
  getDefaultWallets,
  RainbowKitProvider,
} from "@rainbow-me/rainbowkit";
import { chain, configureChains, createClient, WagmiConfig } from "wagmi";
import { alchemyProvider } from 'wagmi/providers/alchemy';
import Aos from "aos";

const isProduction = process.env.REACT_APP_ISPROD

//@ts-ignore
const Providers = ({ children }) => {
  return (

    <Provider store={store}>
      {
        //@ts-ignore
      }<RainbowProvider>
      {
        //@ts-ignore
      }<WalletProvider>
      {children}
    </WalletProvider>
    </RainbowProvider>
    </Provider>

  );
};
//@ts-ignore
const WalletProvider = ({ children }) => {
  return (
    <>
      <Updaters />
      <ProtocolProvider>
        {
          //@ts-ignore
        }<AppContent>{children}</AppContent>
      </ProtocolProvider>
    </>
  );
};
//@ts-ignore
const RainbowProvider = ({ children }) => {
  const { chains, provider } = configureChains(
    ConfigChains,
    [
      alchemyProvider({ apiKey: process.env.REACT_APP_ALCHEMY_ETH_API_KEY }),
      // publicProvider()
    ]
  );

  const { connectors } = getDefaultWallets({
    appName: "MahaDAO NFT",
    chains,
  });

  const wagmiClient = createClient({
    autoConnect: true,
    connectors,
    provider,
  });

  return (
    <WagmiConfig client={wagmiClient}>
      <RainbowKitProvider
        chains={chains}
        showRecentTransactions={true}
      >
        {children}
      </RainbowKitProvider>
    </WagmiConfig>
  );
};

//@ts-ignore
const AppContent = ({ children }) => {
  const core = useCore();
  console.log("core", core)
  // const { ethereum } = useWallet();
  const setChainId = useGetUpdateActiveChainId();

  useEffect(() => {
    if (window.ethereum)
      // @ts-ignore
      window.ethereum.on('chainChanged', (chainId) => {
        console.log('chain changed', chainId);
        setChainId(chainId);
      });
  }, [window.ethereum]);

  if (!core) return <div />;

  return (
    <ModalsProvider>
      <SnackbarProvider
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        maxSnack={2}
        autoHideDuration={2500}
      >
        <>
          {/* <Popups /> */}
          {children}
        </>
      </SnackbarProvider>
    </ModalsProvider>
  );
};

export let isMobileGlobal = false;

const App = () => {
  const makeUnPassive = (ev: any) => {
    ev.preventDefault();
  };

  useEffect(() => {
    document.body.addEventListener('touchmove', makeUnPassive, { passive: true });
    return () => document.body.removeEventListener('touchmove', makeUnPassive);
  }, []);


  useEffect(() => {
    Aos.init();
  }, [])

  return (
    //@ts-ignore
    <Providers>
      <Router>
        <Navigation />
      </Router>
    </Providers>
  );
};

export default App;

// if (isProduction) console.log = function () { };
